<template>
	<div class="gpt_container">
	  <div class="box">
		<input type="text" class="input" v-model.trim="value" @keydown.enter="sendMsg">
		<div class="answer">
			<div class="your" v-for="(item,index) in dialogueList" :key="index">
				你的问题 : {{ item.your }}
				<br />
				<br />
				回答 : {{ item.answer }}
			</div>
		</div>
	  </div>
	</div>
</template>

<script>
import { sendMsg } from "../utils"
export default {
	name: "chatGPT",
	data() {
		return {
			value: "",
			answerShow: false,
			dialogueList: [],
		};
	},
    methods: {
      async sendMsg() {
		let answer = await sendMsg(this.value)

		// 删除answer中字符串的开头的？号
		answer = answer.replace(/^\?/,'')

		this.dialogueList.push({
			your: this.value,
			answer: answer
		})
		this.value = '';
      },
   },
};
</script>
<style lang="scss" scoped>
.gpt_container {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	background: url(../assets/bg.jpg) no-repeat;
	background-size:100% 100%;
	background-attachment:fixed;

	.box {
		position: relative;
		width: 50%;
		min-height: 50%;
		background: rgba(0,0,0,0.5);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		-webkit-backdrop-filter: blur(5px);
		backdrop-filter: blur(5px);
		box-shadow: 20px 20px 32px 0 rgb(0 0 0 / 55%);

		.input {
			position: absolute;
			top: 10%;
			width: 80%;
			height: 50px;
			font-size: 28px;
			color: #fff;
			background-color: rgba(0,0,0,0.5);
		}

		.answer {
			position: absolute;
			top: 30%;
			width: 80%;	
			height: auto;
			background-color: rgba(32, 32, 32, 0.5);

			.your {
				padding: 10px 20px;
				color: #fff;
			}
		}
	}
}
</style>
